import { useContext } from 'react';
import {Context} from '../../Context/Theme';
import './Trending.css';
function Trending(){
    const {theme, setTheme} = useContext(Context);
    return(
        <>
        <section className={`trending ${theme}`}>
            <p><i class='bx bx-error'></i></p>
            <h1>Trending Page not Found</h1>
        </section>
        
        </>
    )
}

export default Trending