import './Main.css'
import { Link } from 'react-router-dom';
import {useEffect, useState, useContext} from 'react';
import {Context} from '../../Context/Theme';

  


function Main() {
    const {theme} = useContext(Context);
    const [more,setMore] = useState(false)
    const [users, setUsers] =useState([])
    useEffect(() =>{
       fetch('https://reqres.in/api/users?page=1')
       .then((response) => response.json())
       .then((data) => {
            setUsers(data.data)
       })
    },[])
    
    // const timer = setTimeout(() => {
    //    console.log(window.location.reload())
    //   }, 1000)
 return(
     <>
        
     <offcanvas className="offcan">
        <div className='offcan-box' id='offcan'>
        <div className={`offcanvas-content ${theme}`} >
          <ul className="link-list">
              <li className="link-item">
                  <Link activeClassName='item-link-active' to='/' className='item-link'><i class='bx bx-home'></i> Home</Link>
              </li>
              <li className="link-item">
                  <Link activeClassName="item-link-active" to='/trending' className='item-link'><i class='bx bxs-flame'></i> Trending</Link>
              </li>
              <li className="link-item">
                  <Link to='/subsc' className='item-link'><i class='bx bx-collection'></i> Subscriptions</Link>
              </li>
              <li className="link-item">
                  <Link to='/library' className='item-link'><i class='bx bx-folder'></i> Library</Link>
              </li>
              <li className="link-item">
                  <Link to='/history' className='item-link'><i class='bx bx-history'></i> History</Link>
              </li>
              <li className="link-item">
                  <Link to='/watch' className='item-link'><i class='bx bx-alarm'></i> Watch later</Link>
              </li>
              <li className="link-item">
                  <Link to='/favourite' className='item-link'><i class='bx bx-star'></i> Favourites</Link>
              </li>
              <li className="link-item">
                  <Link to='/liked' className='item-link'><i class='bx bx-heart'></i>Liked videos</Link>
              </li>
              <li className="link-item">
                  <Link to='/music' className='item-link'><i class='bx bx-music'></i> Music</Link>
              </li>
              <li className="link-item">
                  <Link to='/games' className='item-link'><i class='bx bx-joystick'></i> Games</Link>
              </li>
              <li className="link-item">
                  <button onClick={() => {
                      setMore(more===false? true:false);
                      
                  }}  className='shows-link'>
                  <i class='bx bx-chevron-down' ></i> Show more</button>
              </li>
          </ul>
          <dialog open={more} className={`sub-box ${theme}`}>
                <h3>Subscriptions</h3>
                <ul className='users-list'>
                   {users.map((user) => {
                       return(
                        <li key={user.id}>
                        <Link className='users-link' to={`/channel/${user.id}`}>
                            <img className='user-img' width='30' height='30' src={user.avatar} alt="" />
                            <p>{user.first_name} {user.last_name}</p>
                        </Link>
                    </li>
                       )
                   })}
                </ul>
          </dialog>

          <div className="setting-box">
              <Link className='set-link' to='/setting'><i class='bx bx-cog'></i> Setting</Link>
          </div>
          

           
        </div>
        <button id='off-btn' onClick={() =>{
                let offcan = document.querySelector('#offcan');
                let offcanBtn = document.querySelector('#off-btn');
                offcan.style.display= 'none'
                offcan.style.transition = '1s'
                offcanBtn.style.display = 'none'
            }}>&times;</button>
        </div>
     </offcanvas>
     <div className="sidebar">
         <div className={`sidebar-box ${theme}`}>
             <div className={`sidebar-content ${theme}`}>
                 <ul>
                    
                    <li>
                        <Link className='side-link' to='/'>
                        <i class='bx bx-home' ></i>
                        <span>Home</span>
                        </Link>
                    </li>
                    <li>
                        <Link className='side-link' to='/navigation'>
                        <i class='bx bxs-compass'></i>
                        <span>Navigator</span>
                        </Link>
                    </li>
                    <li>
                        <Link className='side-link' to='/subsc'>
                        <i class='bx bx-collection'></i>
                        <span>Subscriptions</span>
                        </Link>
                    </li>
                    <li>
                        <Link className='side-link'  to='/library'>
                        <i class='bx bxs-videos'></i>
                        <span>Library</span>
                        </Link>
                    </li>

                 </ul>
             </div>
         </div>
     </div>
    
     </>

 )
}

export default Main