import './App.css';
import Header from './Components/Header/Header';
import Main from './Components/Main/Main';
import { Route, Switch} from 'react-router-dom';
import Home from './Pages/Home/Home';
import Channel from './Pages/Channel/Channel';
import Trending from './Pages/Trending/Trending';
import Subsc from './Pages/Subsc/Subsc';
import Library from './Pages/Librarry/Library';
import History from './Pages/History/History';
import Watch from './Pages/Watch/Watch';
import Favourite from './Pages/Favourite/Favourite';
import Liked from './Pages/Liked/Liked';
import Music from './Pages/Music/Music';
import Games from './Pages/Games/Games';
import Video from './Pages/Vidoe/Video';


function App() {
  return (
<>
<Header/>
<Main/>
<Switch>
  <Route path='/' component={Home} exact/>
  <Route path='/trending' component={Trending} exact/> 
  <Route path='/subsc' component={Subsc} exact/>
  <Route path='/library' component={Library} exact/>
  <Route path='/history' component={History} exact/>
  <Route path='/watch' component={Watch} exact/>
  <Route path='/favourite' component={Favourite} exact/>
  <Route path='/liked' component={Liked} exact/>
  <Route path='/music' component={Music} exact/>
  <Route path='/games' component={Games} exact/>
  
  <Route path='/video/:videoId' component={Video}/>

  <Route path='/channel/:id' component={Channel} />
</Switch>

</>
  );
}

export default App;
