import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter } from 'react-router-dom';
import App from './App';
import {Provider as ThemeProvider} from './Context/Theme';


ReactDOM.render(
  <React.StrictMode>
    <ThemeProvider>
    <BrowserRouter>
    <App />
    </BrowserRouter>
   </ThemeProvider>
  </React.StrictMode>,
  document.getElementById('root')
);

